@use './scss/' as *;
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,600;0,700;1,300;1,400;1,600&display=swap');

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
    @include tablet {
        font-size: 50%;
    }
    @include mobile {
        font-size: 37.5%;
    }
}

body {
    font-family: $font-family;
    font-weight: 400;
    line-height: 1.5;

    background-color: $body-bg;
    color: $txt-color;
    @include mobile {
        margin-bottom: 3rem;
    }
}

a {
    text-decoration: none;
    color: unset;
}
@media (hover: hover) {
    a:hover {
        color: $main-color;
        transition: 0.2s ease;
    }
}

input {
    outline: none;
}

button {
    outline: none;
    &:hover {
        cursor: pointer;
    }
}

ul {
    list-style: none;
}

img {
    min-width: 100%;
}

iframe {
    border: 0;
}

.container {
    max-width: 1660px;
    margin: auto;
}

.mb-3 {
    margin-bottom: 3rem;
}

.mb-2 {
    margin-bottom: 2rem;
}

.section {
    padding: 0 2rem;
}

p {
    font-size: 1.6rem;
}
